export enum TokenType {
    COLOR = 'color',
    DIMENTION = 'dimention',
    DURATION = 'duration',
    FONT = 'font',
    URL = 'url',
    TYPOGRAPHY = 'typography',
    GRADIENT = 'gradient',
    SHADOW = 'shadow',
    TRANSITION = 'transition',
}

export type TokenAttributes = {
    category: string;
    type?: string;
    item?: string;
    subitem?: string;
};

export type DesignToken = {
    value: string;
    comment?: string;
    filePath: string;
    isSource: boolean;
    original: { [key: string]: string };
    name: string;
    attributes: TokenAttributes;
    path: string[];
};
